$global-variables: (customBlocksName: eightshift-block,mainContentWidth: 12,maxCols: 12,baseFont: 'DM Sans',navbarHeight: 5rem,zIndex: (header: 99,drawer: 100,overlay: 98,),breakpoints: (mobile: 480,tablet: 960,desktop: 1440,large: 1920,),grid: (gutter: 1vw,sidePadding: 6.75vw,),sectionSpacing: (min: -300,max: 300,step: 10,),sectionInSpacing: (min: 0,max: 300,step: 10,),colors: (primary700: #4700AB,primary500: #610BEF,primary300: #BFBEFC,black: #14142B,gray900: #2C2C40,gray800: #434355,gray700: #5B5B6B,gray600: #727280,gray500: #8A8A95,gray400: #A1A1AA,gray300: #B9B9BF,gray200: #D0D0D5,gray100: #E8E8EA,white: #FFFFFF,),);
@import '../scripts/components/custom-slider/custom-slider';

// WP styles.
@import '@wordpress/editor/build-style/style-rtl.css';
@import '@wordpress/editor/build-style/style.css';
@import '@wordpress/components/build-style/style.css';
@import '@wordpress/block-editor/build-style/style.css';
@import '@wordpress/format-library/build-style/style.css';

// ES components.
@import './../blocks/init/src/Blocks/assets/styles/application-blocks-editor-components.scss';

// Playground styles.
$sidebar-width: 280px;
$light-gray-500: #E2E4E7;
$break-small: 600px;

.playground {
	@media (min-width: #{ ($break-small) }) {
		width: calc(100% - #{$sidebar-width});
	}

	padding-top: 20px;
	padding-bottom: 20px;
}

.playground__sidebar {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	width: $sidebar-width;
	border-left: 1px solid $light-gray-500;
	height: auto;
	overflow: auto;
	-webkit-overflow-scrolling: touch;

	// Temporarily disable the sidebar on mobile
	display: none;

	@media (min-width: #{ ($break-small) }) {
		display: block;
	}

	.block-editor-block-card {
		padding: 16px;
	}
}
